































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList.ts';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Charts from '@/components/Charts.vue';
import _ from 'lodash'

@Component({
  components: {
    Tabs,
    Charts
  },
})

export default class Statistics extends Vue {
  type = '-';
  recordTypeList = recordTypeList;

  beforeCreate() {
    this.$store.commit('fetchRecords');
  }


  get keyValueList(){
    const today =new Date()
    const array =[]
    for(let i=0; i<=29; i++){
      const dataString = dayjs(today).subtract(i,'day').format('YYYY-MM-DD')
      const found = _.find(this.groupedList,{title:dataString})
      array.push({key:dataString,value:found?.total})
    }
    array.sort((a,b)=>{
      if(a.key>b.key){
        return 1
      }else if(a.key===b.key){
        return 0
      }else{
        return -1
      }
    })
    return array
  }

  get chartOptions(){
    const  keys = this.keyValueList.map(item=>item.key)
    const values = this.keyValueList.map(item=>item.value)
    return {
      grid:{
        right:0,
        left:0
      },
      tooltip:{show:true},

      xAxis: {
        type: 'category',
        data: keys,
        axisTick:{alignWithLabel:true},
        axisLine:{lineStyle:{color: '#666'}},
        axisLabel:{
          formatter:function (value: string,){
            return value.substr(5)
          }
        }
      },
      yAxis: {
        type: 'value',
        show:false
      },
      series: [{
        symbolSize:10,
        symbol:'circle',
        itemStyle:{color:'#666',borderColor:'#666'},
        data: values,
        type: 'line',
      }]
    }
  }
  mounted(){

    const div = this.$refs.chartWrapper as HTMLDivElement
    div.scrollLeft = div.scrollWidth
  }
  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {
    const {recordList} = this;
    const newList = clone(recordList)
        .filter((r) => r.type === this.type)
        .sort(
            (a, b) => dayjs(b.recordTime).valueOf() - dayjs(a.recordTime).valueOf()
        );
    if (newList.length === 0) {
      return [];
    }
    type Result = { title: string; total?: number; items: RecordItem[] }[];
    const result: Result = [
      {
        title: dayjs(newList[0].recordTime).format('YYYY-MM-DD'),
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.recordTime), 'day')) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.recordTime).format('YYYY-MM-DD'),
          items: [current],
        });
      }
    }
    result.map((group) => {
      group.total = group.items.reduce((sum, item) => {
        return sum + item.amount;
      }, 0);
    });
    return result;
  }

  tagString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.join('，');
  }

  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, 'day')) {
      return '今天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天';
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天';
    } else if(day.isSame(now,'year')){
      return day.format('M月D日');
    }else{
      return day.format('YYYY年M月D日')
    }
  }
}
